html, *, :before, :after {
  box-sizing: border-box;
}

.container {
  min-width: 320px;
  max-width: 414px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 930px;
    padding-left: 47px;
    padding-right: 47px;
  }
}

@media screen and (main-width: 1280px) {
  .container {
    max-width: 1110px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

:root {
  --modal-backdrop: #2e2f42;
  --modal-background: #fcfcfc;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
  display: block;
}

